import { getAuth, createUserWithEmailAndPassword } from "firebase/auth";

import Header from 'components/Header';
import React, { useEffect, useState } from 'react'; 
import styles from"./SignUp.module.css";
import { Link } from "react-router-dom";

function SignUp(){

    const [name, setName] = useState("");
    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");
    const [passwordCheck, setPasswordCheck] = useState("");

    //error
    const [error, setError] = useState("");
    const [pwdError, setPwdError ] = useState(false);
    
    //complete
    const [compelete, setComplete] = useState(false);

      


    const onChange = (e) => {
        const {
            target : {name, value},
        } = e;
        
        if( name === "name" ){
            setName(value);
        } else if( name === "email" ){
            setEmail(value);
        }else if( name === "password" ){
            setPassword(value);
        }else if( name === "passwordCheck" ){
            setPasswordCheck(value);
        }
    }

    useEffect(() => {
        if(password !== passwordCheck){
            setPwdError(true);
        }else if(password === passwordCheck){
            setPwdError(false);
        }
    },[passwordCheck]);
    

    const onSubmit = async (e) => {
        e.preventDefault();

        const auth = getAuth();
        createUserWithEmailAndPassword(auth, email, password)
        .then((userCredential) => {
            // Signed in
            const user = userCredential.user;
            console.log(user)
            setComplete(true)
        })
        .catch((error) => {
            const errorCode = error.code;
            const errorMessage = error.message;
            console.log(errorCode,errorMessage);
            setError(errorCode);
        });
    }

    return (
        <div className={styles.signUp_container}>
            <Header title=""/>
            <div className={styles.singUp_wrap}>
                    <form onSubmit={onSubmit}>
                        <div className={styles.ipt_wrap}>
                            <p className={styles.label}></p>
                                <p>
                                    <input 
                                        type="text"
                                        name="name"
                                        value={name}
                                        onChange={onChange}
                                        placeholder=" ."
                                        className={styles.ipt}
                                        required
                                    />
                            </p>
                        </div>
                        <div className={styles.ipt_wrap}>
                            <p className={styles.label}></p>
                            <p>
                                <input 
                                    type="email"
                                    name="email"
                                    value={email}
                                    onChange={onChange}
                                    placeholder=" ."
                                    className={styles.ipt}
                                    required
                                />
                            </p>
                            <p className={`${styles.help}`}>
                                {(error === 'auth/email-already-in-use')? "   .": ""}
                            </p>
                        </div>
                        <div className={styles.ipt_wrap}>
                            <p className={styles.label}></p>
                            <p>
                                <input 
                                    type="password"
                                    name="password"
                                    value={password}
                                    onChange={onChange}
                                    placeholder=" ."
                                    className={styles.ipt}
                                    required
                                />
                            </p>
                            <p className={`${styles.help}`}>
                                {(error === 'auth/weak-password')? "  6  .": ""}
                            </p>
                        </div>
                        <div className={styles.ipt_wrap}>
                        <p className={styles.label}> </p>
                            <p>
                                <input 
                                    type="password"
                                    name="passwordCheck"
                                    value={passwordCheck}
                                    onChange={onChange}
                                    placeholder=" ."
                                    className={styles.ipt}
                                    required
                                />
                            </p>
                            <p className={`${styles.help}`}>{ (pwdError) ? '  ' : '' }</p>
                        </div>

                        <input type="submit" value="" className={styles.signup_btn}/>
                    </form>
            </div>

            { (compelete) ? 
                <div className={styles.conplete_popup}>
                    <div className={styles.popup_container}>
                        <img src={`${process.env.PUBLIC_URL}/images/sign_up_complete.png`} alt=" ."/>
                        <p> .</p>
                        <Link to="/"><button type="button" className={styles.complete_btn}></button></Link>
                    </div>
                </div>
                :
                ""
            }
           
        </div>
    );
}

export default  SignUp;