import Header from 'components/Header';
import Navigation from 'components/Navigation';
import React from 'react'; 
import styled from 'styled-components';
import { Link } from 'react-router-dom';
import styles from"./MyPage.module.css";

const List = styled.div`
    /*   */
    border-top: ${props => props.bordertop ? "1px solid #dfdfdf" : "unset"};
    border-bottom:1px solid #dfdfdf;
    padding: 15px 10px;
    font-size: 16px;
`;


function Mypage(){

    return (
        <>
           <Header title=""/>
           <div className="sub_content">
                <div className={styles.profileWrap}>
                    <div className={styles.leftBox}>
                        <p className={styles.name}><span></span> </p>
                        <p className={styles.produce}>   </p>
                    </div>
                    <div className={styles.rightBox}>
                        <button></button>
                    </div>
                </div>

                <ul className={styles.stampList}>
                    <li>
                        <Link to="/stamp_accumulate">
                            <div className={styles.item}>
                                <div className={styles.icon}></div>
                                <p className={styles.title}> </p>
                            </div>
                        </Link>
                    </li>
                    <li>
                        <Link to="/stamp">
                            <div className={styles.item}>
                                <div className={styles.icon}></div>
                                <p className={styles.title}></p>
                                {/*  ,  /stamp_list  */}
                            </div>
                        </Link>
                    </li>
                </ul>

                <div className={styles.StoreBtnWrap}>
                    <Link to="/stamp_application">
                        <button className={styles.orangeBtn}>
                                
                        </button>
                    </Link>
                </div>
                <div className={styles.StoreBtnWrap}>
                    <div className={styles.StoreInfo}>
                        <p className={styles.title}> <span className={styles.orange}>  </span> .</p>
                        <p className={styles.done}></p>
                        <p className={styles.date}>2022.01.01</p>
                    </div>
                </div>
                

                <Link to="/editmember">
                    <List bordertop> </List>
                </Link>
                <Link to="/store_regist">
                    <List></List>
                </Link>
                <Link to="/stamp_modify">
                    <List>  </List>
                </Link>
                
           </div>
           <Navigation />
        </>
    );
}

export default  Mypage;