import Header from 'components/Header';
import Navigation from 'components/Navigation';
import React from 'react'; 
import { useNavigate } from 'react-router-dom';
import styles from"./StampAccumulate.module.css";


function StampAccumulate(){

    let navigate = useNavigate();

    return (
        <>
            <Header title=" "/>
            <div className={styles.stampContainer}>
                <p className={styles.title}> </p>
                <img src={`${process.env.PUBLIC_URL}/images/sobok_logo_and_title.svg`} alt="   " className={styles.logo}/>
                <p className={styles.phoneNumber}>010-0000-0000</p>
                <p className={styles.help}>    .</p>

                <div className={styles.btnWrap}>
                    <button className={styles.okBtn} onClick={() => {navigate(-1)}}></button>
                </div>
            </div>
            <Navigation />
        </>
    );
}

export default  StampAccumulate;