import React from 'react'; 
import Header from 'components/Header';
import Navigation from 'components/Navigation';

import { Link } from 'react-router-dom';

import styles from './EditMember.module.css';
import styled from 'styled-components';

const List = styled.div`
    /*   */
    border-top: ${props => props.bordertop ? "1px solid #dfdfdf" : "unset"};
    border-bottom:1px solid #dfdfdf;
    padding: 15px 10px;
    font-size: 16px;
`;

function EditMember(){
    return (
        <>
           <Header title=" "/>
           <div className="sub_content">
                <div className={styles.Editform}>
                    <div className={styles.ipt_wrap}>
                        <p className={styles.label}></p>
                        <p>
                            <input 
                                type="email"
                                name="email"
                                placeholder=""
                                className={styles.ipt}
                                required
                            />
                        </p>
                    </div>
                    <div className={styles.ipt_wrap}>
                        <p className={styles.label}><b>*</b></p>
                        <p>
                            <input 
                                type="password"
                                name="password"
                                placeholder=" ."
                                className={styles.ipt}
                                required
                            />
                        </p>
                    </div>
                    <div className={styles.ipt_wrap}>
                        <p className={styles.label}> <b>*</b></p>
                        <p>
                            <input 
                                type="password"
                                name="password"
                                placeholder=" ."
                                className={styles.ipt}
                                required
                            />
                        </p>
                    </div>
                    <div className={styles.ipt_wrap}>
                        <p className={styles.label}><b>*</b></p>
                        <p>
                            <input 
                                type="text"
                                name="name"
                                placeholder=" "
                                className={styles.ipt}
                                required
                            />
                        </p>
                    </div>
                    <div className={styles.ipt_wrap}>
                        <p className={styles.label}><b>*</b></p>
                        <p>
                            <input 
                                type="email"
                                name="email"
                                placeholder=" "
                                className={styles.ipt}
                                required
                            />
                        </p>
                    </div>
                </div>
                <List></List>
                <div className={styles.btnWrap}>
                    <input type="submit" value="" className={styles.ok_btn}/>
                </div>
           </div>
           <Navigation />
        </>
    );
}

export default  EditMember;