import Header from 'components/Header';
import Navigation from 'components/Navigation';
import React from 'react'; 
import styles from"./StampIssued.module.css";

function StampIssued(){
    return (
        <>
           <Header title=" "/>
            <div className={styles.stampContainer}>
                <p className={styles.title}> </p>
                <img src={`${process.env.PUBLIC_URL}/images/sobok_logo_and_title.svg`} alt="   " className={styles.logo}/>
                <p className={styles.help}>   .</p>

                <label className={styles.label}> <b>*</b></label>
                <div className={styles.iptWrap}>
                    <input type="text" placeholder="010-0000-0000" className={styles.iptWrap}/>
                </div>

                <label className={styles.label}> <b>*</b></label>
                <div className={styles.iptWrap}>
                    <input type="text" placeholder="1  " className={styles.iptWrap}/>
                </div>

                <div className={styles.btnWrap}>
                    <button className={styles.okBtn}></button>
                </div>
            </div>
            <Navigation />
        </>
    );
}

export default  StampIssued;