import Header from 'components/Header';
import Navigation from 'components/Navigation';
import React, { useState } from 'react'; 
import { useNavigate } from 'react-router-dom';
import styles from './StoreRegist.module.css';


function StoreRadioWrap( {title , name , onClick} ){

    return (
        <div className={styles.ipt_wrap}>
            <div className={styles.label}>{title} <b>*</b></div>
            <ul className={styles.radio_wrap}>
                <li>
                    <input 
                        type="radio" 
                        name={name} 
                        id={`${name}_true`} 
                        value="" 
                        onClick={onClick}
                    /> 
                    <label  htmlFor={`${name}_true`}>
                        
                    </label>
                </li>
                <li>
                    <input 
                        type="radio"  
                        name={name} 
                        id={`${name}_false`} 
                        value="" 
                        onClick={onClick}
                    /> 
                    <label htmlFor={`${name}_false`}>
                        
                    </label>
                </li>
            </ul>
        </div>
    );
}


function StoreRegist(){

    const [storeName, setStoreName] = useState(""); //
    const [explanation, setExplanation] = useState(""); // 
    const [tel, setTel] = useState(""); //
    const [storeTime, setStoreTime] = useState(""); //
    const [closed, setClosed] = useState(""); //
    const [homepage, setHomepage] = useState(""); //

    //
    const [socket, setSocket] = useState(""); //
    const [bigTable, setBigTable] = useState(""); // 
    const [parking, setParking] = useState(""); //
    const [pets, setPets] = useState(""); //
    const [fullGlass, setFullGlass] = useState(""); //
    const [smoking, setSmoking] = useState(""); //
    const [noKids, setNoKids] = useState(""); //
    const [sofa, setSofa] = useState(""); //
    const [terrace, setTerrace] = useState(""); //
    const [rooftop, setRooftop] = useState(""); //

    let navigate = useNavigate();


    const onSubmit = (event) => {
        event.preventDefault();
    }

    const onChange = (event) => {
        event.preventDefault();

        const{
            target : {name, value}
        } = event;

        if(name==="store_name"){ setStoreName(value); }
        if(name==='explanation'){ setExplanation(value); }
        if(name==='store_tel'){ setTel(value); }
        if(name==='store_time'){ setStoreTime(value); }
        if(name==='store_closed'){setClosed(value);}
        if(name==='store_homepage'){setHomepage(value);}
    }  

    const onClick = (event) => {
        const{
            target : {name, value}
        } = event;

        if(name === 'power_socket'){ setSocket(value); }
        if(name === 'big_table'){ setBigTable(value); }
        if(name === 'parking'){ setParking(value); }
        if(name === 'pets'){ setPets(value); }
        if(name === 'full_glass'){ setFullGlass(value); }
        if(name === 'smoking_room'){ setSmoking(value); }
        if(name === 'no_Kids'){ setNoKids(value); }
        if(name === 'sofa'){ setSofa(value); }
        if(name === 'terrace'){ setTerrace(value); }
        if(name === 'rooftop'){ setRooftop(value); }
        

    } 

    return (
        <div>
           <Header title=""/>
           <div className="sub_content">
                <form onSubmit={onSubmit}>
                    <div className={styles.title}></div>

                    <div className={styles.ipt_wrap}>
                        <div className={styles.label}> <b>*</b></div>
                        <input 
                            type="text" 
                            name="store_name" 
                            className={styles.ipt}
                            required
                            value={storeName}
                            onChange={onChange}
                            placeholder=" ." 
                        />
                    </div>

                    <div className={styles.ipt_wrap}>
                        <div className={styles.label}></div>
                        <input 
                            type="text" 
                            name="explanation" 
                            className={styles.ipt}
                            value={explanation}
                            onChange={onChange}
                            placeholder=" ." 
                        />
                    </div>

                    <div className={styles.ipt_wrap}>
                        <div className={styles.label}></div>
                        <input 
                            type="text" 
                            name="store_tel" 
                            className={styles.ipt}
                            value={tel}
                            onChange={onChange}
                            placeholder=" ." 
                        />
                    </div>

                    {/*  */}
                    <div className={styles.ipt_wrap}>
                        <div className={styles.label}> <b>*</b></div>
                        <input 
                            type="text" 
                            name="store_address" 
                            required
                            className={styles.ipt}
                            placeholder=" ." 
                        />
                        <input 
                            type="text" 
                            name="store_address2" 
                            required
                            className={styles.ipt}
                            placeholder=" ." 
                        />
                    </div>


                    <div className={styles.ipt_wrap}>
                        <div className={styles.label}> <b>*</b></div>
                        <textarea 
                            type="text" 
                            name="store_time" 
                            required
                            className={styles.ipt}
                            value={storeTime}
                            onChange={onChange}
                            placeholder=" ." 
                        ></textarea>
                    </div>

                    <div className={styles.ipt_wrap}>
                        <div className={styles.label}></div>
                        <textarea 
                            type="text" 
                            name="store_closed" 
                            className={styles.ipt}
                            value={closed}
                            onChange={onChange}
                            placeholder=" ." 
                        ></textarea>
                    </div>

                    <div className={styles.ipt_wrap}>
                        <div className={styles.label}></div>
                        <input 
                            type="text" 
                            name="store_homepage" 
                            className={styles.ipt}
                            value={homepage}
                            onChange={onChange}
                            placeholder="  ." 
                        />
                    </div>


                    <StoreRadioWrap title="" name="power_socket" onClick={onClick}/>
                    <StoreRadioWrap title="" name="big_table" onClick={onClick}/>
                    <StoreRadioWrap title="" name="parking" onClick={onClick}/>
                    <StoreRadioWrap title="" name="pets" onClick={onClick}/>
                    <StoreRadioWrap title="" name="full_glass" onClick={onClick} />
                    <StoreRadioWrap title="" name="smoking_room" onClick={onClick} />
                    <StoreRadioWrap title="" name="no_Kids" onClick={onClick} />
                    <StoreRadioWrap title="" name="sofa" onClick={onClick} />
                    <StoreRadioWrap title="" name="terrace" onClick={onClick} />
                    <StoreRadioWrap title="" name="rooftop" onClick={onClick} />
                    
                    <ul className={styles.btn_container}>
                        <li>
                            <button 
                                type="button" 
                                className={styles.cancelBtn}
                                onClick={() => {navigate(-1)}}
                            ></button>
                        </li>
                        <li>
                            <button type="submit" className={styles.submitBtn}></button>
                        </li>
                    </ul>
                </form>
           </div>
           <Navigation />
        </div>
    );
}

export default  StoreRegist;