import React from 'react'; 
import styles from"./SignupComplete.module.css";

import { Link } from 'react-router-dom';

function SignupComplete(){
    return (
        <>
        <div className="sub_content">
                <div className={styles.Container}>
                    <img src={`${process.env.PUBLIC_URL}/images/sobok_logo_and_title.svg`} alt="  " className={styles.logo}/>
                    <p className={styles.title01}> .</p>
                    <div className={styles.contentWrap}>
                        <span className={styles.orange}>  ?</span><br/>
                           <br/>
                         &gt;   <br/>
                          .
                    </div>
                    <ul className={styles.buttonWrap}>
                        <li>
                            <Link to="/">
                                <button className={styles.ok_btn}> </button>
                            </Link>
                        </li>
                    </ul>
                </div>
        </div>
    </>
    );
}

export default  SignupComplete;