import Header from 'components/Header';
import Navigation from 'components/Navigation';
import React from 'react'; 
import styles from"./StampModify.module.css";

function StampModify(){
    return (
        <>
           <Header title="  "/>
           <div className={styles.stampContainer}>
                <p className={styles.title}>  </p>

                <div className={styles.ipt_wrap}>
                    <p className={styles.label}>5  <b>*</b></p>
                    <p>
                        <input 
                            type="text"
                            name="name"
                            placeholder="5   ."
                            className={styles.ipt}
                            required
                        />
                    </p>
                </div>
                <div className={styles.ipt_wrap}>
                    <p className={styles.label}>  <b>*</b> <span className={styles.important}>500x700 ( )</span></p>
                    <p>
                        <input type="file" required className={styles.file}/>
                    </p>
                </div>
                <div className={styles.ipt_wrap}>
                    <p className={styles.label}>10   <b>*</b></p>
                    <p>
                        <input 
                            type="text"
                            name="name"
                            placeholder="10   ."
                            className={styles.ipt}
                            required
                        />
                    </p>
                </div>
                <div className={styles.ipt_wrap}>
                    <p className={styles.label}>  <b>*</b> <span className={styles.important}>500x700 ( )</span></p>
                    <p>
                        <input type="file" required className={styles.file}/>
                    </p>
                </div>

                <div className={styles.btnWrap}>
                    <button className={styles.okBtn}></button>
                </div>
           </div>
           <Navigation />
        </>
    );
}

export default  StampModify;