import Header from 'components/Header';
import Navigation from 'components/Navigation';
import React from 'react'; 
import { Link } from 'react-router-dom';
import styles from"./CheckCoupon.module.css";

function CheckCoupon(){
    return (
        <>
           <Header title=""/>
           <div className={styles.cuponContainer}>
                <div className={styles.storeTitle}>
                    <div class={styles.logo} style={{backgroundColor:'pink'}}></div> {/*    background-image  */}
                        
                </div>
                <div class={styles.cuponWrap}>
                    <img src={`${process.env.PUBLIC_URL}/images/stamp_product.png`} alt="   " className={styles.stampProduct} />
                    <p className={styles.title}> :   </p>
                    <p className={styles.cupon}> : 1</p>
                </div>
                <div className={styles.btnWrap}>
                    <Link to="/shipping">
                    <button className={styles.okBtn}> </button>
                    </Link>
                </div>
           </div>
           <Navigation />
        </>
    );
}

export default  CheckCoupon;